
import Icon from 'vux/src/components/icon/index.vue'
import Search from 'vux/src/components/search/index.vue'
import numberPad from 'vux/src/tools/number/pad.js'

import getDoneService from '../service/getDoneService'
import business from './maps/business'
import { setTimeout } from 'timers';
export default {
data(){
return{
Content:[],
pageNo:0,
upScroll: null,
hasNext:true,
}
},
methods:{


list(num){
let jsonPage={
page:num,
start:0,
limit:10
}
getDoneService.getDoneList(jsonPage).then(res=>{

for(let i = 0 ; i < res.tableContent.length ; i++){

res.tableContent[i].processName=business[res.tableContent[i].businessKey.split('_')[0]];
}
this.Content=this.Content.concat(res.tableContent);
this.upScroll.endSuccess(res.tableContent.length,this.hasNext);
})
},
goDetail(code){
this.$router.push({
path : '/detail',
query : {
transCode : code,
status : 'done'
}
})

},

upCallback(){
this.list (++this.pageNo);
},
},
components:{
Icon,
Search
},
filters: {

filterTime(val) {
let date = new Date(val.replace(/-/g, '/'));
return `${date.getFullYear()}-${numberPad(date.getMonth() + 1)}-${numberPad(date.getDate())}`;
}
},
mounted(){
let that=this;
let Mescroll = that.Mescroll;
that.upScroll = new Mescroll("mescroll",{
up:{
use:true,
isBounce: false,
htmlNodata: '<p class="upwarp-nodata">-- END --</p>',
callback:that.upCallback,
page: {
num: that.pageNo, //当前页 默认0,回调之前会加1; 即callback(page)会从1开始
size: 10, //每页数据条数
time: 300 
},
empty: {
warpId:'mescroll',
tip: "暂无相关数据~",
supportTap: false
},
},
down:{
use:false
}
})
},
updated(){

}
}



