
import numberPad from 'vux/src/tools/number/pad.js'
import Icon from 'vux/src/components/icon/index.vue'
import Search from 'vux/src/components/search/index.vue'

import todoService from './../service/todoService'
import businessMap from './maps/business'

export default {
components: {
Icon,
Search,
},
data() {
return {
todoScroll: null, // 滚动对象
listData: [], // 数据对象
page: 1, // 页数
showSmile: false 
}
},
methods: {

getStatusName(item) {
if(item.businessKey){
let business = item.businessKey.split('_');
return businessMap[business[0]] || '未知项';
}

},

filterStatusAndClass(item) {
let obj = {
status: item.status || '',
hasRedPoint: false
};
switch (item.status) {
case '进行中':
if (item.endTime) {
obj.statusClass = 'duty_process_c';
} else {
obj.status = '待处理';
obj.statusClass = 'duty_wait_c';
obj.hasRedPoint = true;
}
break;
case '已生效':
obj.statusClass = 'duty_process_c';
break;
case '将逾期':
obj.statusClass = 'duty_urgent_c';
break;
}
return obj;
},

getTodoList() {
return new Promise((resolve, reject) => {
todoService.getList({
page: this.page
}).then(data => {
let tmpList = [];
tmpList = data.tableContent && data.tableContent.map(item => {
return Object.assign({}, {
statusName: this.getStatusName(item),
requireName: item.requireName || '见详情',
code: item.businessKey || '',
crtName: item.crtName,
time: item.crtTime,
}, this.filterStatusAndClass(item));
});
this.listData = this.page === 1 ? tmpList : this.listData.concat(tmpList);
resolve(tmpList);
})
})
},

goDetail(code) {
this.$router.push({
path : '/detail',
query : {
transCode : code,
status : 'do'
}

})
}
},
filters: {


filterTime(val) {
if(val){
let date = new Date(val.replace(/-/g, '/'));
return `${date.getFullYear()}-${numberPad(date.getMonth() + 1)}-${numberPad(date.getDate())}`;
}

}
},
created() {
},
mounted() {
let Mescroll = this.Mescroll;
this.$nextTick(() => {
if (!this.todoScroll) {
this.todoScroll = new Mescroll("mescroll", {
up: {
use: true,
isBounce: true,
auto: true,
isBoth: false,
callback: (page, mescroll) => {
this.page = page.num;
this.getTodoList().then(data => {
let len = data.length;
let hasNext = len >= page.size;
if (!len && this.page === 1) { //当没有数据的时候，且为第一页
this.showSmile = true;  
}
mescroll.endSuccess(len, hasNext)
});
}
},
down: {
use: true,
isBoth: false,
auto: false, // 初始化不执行
callback: (mescroll) => {
this.page = 1;
this.getTodoList().then((data) => {
if (data.length > 0) { //下拉刷新 重新获取数据
this.showSmile = false; 
}
mescroll.endDownScroll(false);
})
}
}
})
}
})
}
}



