
import getDetailService from './../service/getDetailService.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import Flow from 'vux/src/components/flow/flow.vue'
import FlowState from 'vux/src/components/flow/flow-state.vue'
import FlowLine from 'vux/src/components/flow/flow-line.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import BScroll from 'better-scroll'
import querystring from 'vux/src/tools/querystring/index.js'

export default {
data() {
return {
showLoadding:true,          //loadding状态
formInfo : {},              //任务信息
code : '',                  //任务编码
taskStatus : true,          //是否代办
taskIdInfo : {},            //任务id信息
oldformInfo : {},           //旧接口任务信息
infoList:[],                //工作流列表
viewType:'',                //任务view类型
confirmshow : false,        //任务确认框状态
flowShow:false,             //工作流显示状态
taskTime : 0.1,             //工时
remark : '',                //备注
etc : '',                   //预计交付时间
showPositionValue:false,    //提示框状态
warn:'',                    //提示内容
userList:[],                //任务分配者列表
assigned:{},                //任务被分配者信息
agreeStatus:1,              //是否同意 1同意，0 拒绝
transferUserList:[],        //转办人员列表
transferInfo:{},            //选中转办人员信息
choicedIndex:-1,            //转办人员选中的class
devType:'',                 //业务类型
detailListScroll: null,     // 最外层容器滚动对象
transferListScroll: null,   // 转办列表滚动对象
distributionScroll: null,   //分配给的滚动对象
currentComponent:'',        //当前需要渲染的组件
assignShow:false              
}
},
directives: {
TransferDom
},
components: {
Confirm,
XInput ,
XTextarea ,
Flow,
FlowState,
FlowLine ,
Group,
Toast ,
Cell,
Popup,
Spinner
},
methods: {

getAssignedInfo(item){
this.assigned  = item;
this.assignShow = false;
},

agree(){
this.confirmshow = true;
this.agreeStatus = 1;
this.transferUserList = [];
},

reject(){
this.confirmshow = true;
this.agreeStatus = 0;
this.transferUserList = [];
},

transfer(){
this.confirmshow = true;
getDetailService.getTransferUserList().then( data=>{
this.transferUserList = data.tableContent;
this.$nextTick(() => {
if(!this.transferListScroll) {
this.transferListScroll = new BScroll(this.$refs.transferList, {
click: true,
top: false,
bottom: false

})
}
})
})

},

getTransfer(obj,i){
this.transferInfo = obj;
this.choicedIndex = i;
},

onCancel(){

},

SaveData(){
for(let key in this.oldformInfo){
if(key === 'requirement'||key === 'requirementProject'){
this.oldformInfo[key].etc = this.etc;

if(this.oldformInfo[key].provideType){

this.devType = this.oldformInfo[key].provideType.selection.data.text;
}
}
}
let wfPara ;
if(this.taskIdInfo.tableContent[0].nodeName === '分配测试任务'){
wfPara = {
'taskId' : this.taskIdInfo.tableContent[0].taskId,
'businessKey':this.code,
'testUser':this.assigned.userId,
'devType':this.devType,
'transCode':this.code,
'result':this.agreeStatus,
'comment':this.remark,
'taskTime':this.taskTime
}

}
else{
wfPara = {
'taskId' : this.taskIdInfo.tableContent[0].taskId,
'businessKey':this.code,
'devUser':this.assigned.userId,
'devType':this.devType,
'transCode':this.code,
'result':this.agreeStatus,
'comment':this.remark,
'taskTime':this.taskTime
}
}
let data = {
conn:20000,
list:'trans_form_data',
transCode:this.code,
wfPara:JSON.stringify(wfPara),
jsonData:JSON.stringify(this.oldformInfo)
}
getDetailService.saveAndCommitTask(data).then(data=>{
if(data.success){
this.showPositionValue = true;
this.warn = '提交成功';
setTimeout(()=>{
this.$router.push("/home");
},500)
}
else{
this.showPositionValue = true;
this.warn = data.message;
}
}).catch(error=>{
console.log(error);
this.showPositionValue = true;
this.warn = error.message;
})

},

commitTask(){
let jsonData = {
'transCode':this.code,
'result':this.agreeStatus,
'comment':this.remark,
'taskTime':this.taskTime
}
let data = {
jsonData:JSON.stringify(jsonData)
}
getDetailService.commitTask(data,this.taskIdInfo.tableContent[0].taskId).then( data=>{
if(data.success){
this.showPositionValue = true;
this.warn = '提交成功';
setTimeout(()=>{
this.$router.push("/home");
},500)
}
else{
this.showPositionValue = true;
this.warn = data.message;
}
})

},

onConfirm(){

if(this.transferUserList.length>0){
if(this.remark === ''){
this.showPositionValue = true;
this.warn = '【备注】不能为空';
this.choicedIndex = -1;
}
else if(!this.transferInfo.userId){
this.showPositionValue = true;
this.warn = '【转办人】不能为空'
}
else{
let data = {
userId : this.transferInfo.userId,
userCode : this.transferInfo.userCode,
taskId : this.taskIdInfo.tableContent[0].taskId,
comment : this.remark,
taskTime : this.taskTime

}
getDetailService.transferTask(data).then( data=>{
console.log(data);
if(data.success){
this.showPositionValue = true;
this.warn = '转办成功';
setTimeout(()=>{
this.$router.push("/home");
},500)
}
else{
this.showPositionValue = true;
this.warn = '转办失败'
}
})
}

}

else{

if(this.agreeStatus === 0){
if(this.remark === ''){
this.showPositionValue = true;
this.warn = '【备注】不能为空'
}
else{
if(this.viewType === 'marking'){
this.SaveData()
}
else if(this.viewType === 'view'){
this.commitTask()
}
}
}

else{
if(this.viewType === 'marking'){
if(this.taskIdInfo.dataCount === 1&&this.taskIdInfo.tableContent[0].nodeName === '承接任务'&&this.etc === ''){
this.showPositionValue = true;
this.warn = '【预计交付时间】不能为空';
}
else if(this.formInfo.assignedTo_fgPlanInv&&!this.assigned.userId){
this.showPositionValue = true;
this.warn = '【分配给】不能为空';
}
else{
this.SaveData()
}
}
else{
this.commitTask()
}
}
}
},

getDate(res){
this.etc = res;
},

getAssigned(res){
this.assignShow = res;
}
},
created() {
let code = '',
formId = '',
status = '',
query = querystring.parse(location.search.slice(1));
code = query.transCode;
status = query.status || 'do'
this.code = code;
if(code != '' ){

this.currentComponent = require(`./component/${code.split('_')[0]}Form.vue`).default;
}
if(status === 'done'){
this.taskStatus = false;
}
(async()=>{
let arr = []

await getDetailService.getFormId(this.code).then(data=>{
formId = data[0].uniqueId;
})

await getDetailService.getTaskInfo(this.code).then( data=> {
this.taskIdInfo  = data;
if(data.dataCount!=0){
formId = data.tableContent[0].viewId;
}
})

await getDetailService.getDetailInfo(formId,this.code).then( data=> {

this.formInfo = data;
})

await getDetailService.getViewId(formId).then( data=>{
this.viewType = data[0].viewType;


})
this.showLoadding = false;

})()

getDetailService.getDoInfo(this.code).then( data=> {
this.oldformInfo = JSON.parse(data.tableContent[0].json_data);

})

getDetailService.getUserList().then( data=>{
this.userList = data.tableContent;
})

getDetailService.getFlowList(this.code).then( data=>{
this.infoList = data.tableContent;
})

},
mounted() {

this.$nextTick(() => {
if(!this.detailListScroll){
this.detailListScroll = new BScroll(this.$refs.detailList, {
click:true,
bounce:{
top: false,
bottom: false
}
})
}
})
},
watch:{

assignShow(val){
if(val){
this.$nextTick(() => {
if(!this.distributionScroll) {
this.distributionScroll = new BScroll(this.$refs.distribution, {
click: true,
bounce:{
top: false,
bottom: false
}
})
}
})
}
}
},
}



